<template>
  <div>
    <div class="video-container">
      <img
        :src="url + '?x-oss-process=video/snapshot,t_0,w_0,h_0,f_jpg'"
        width="480"
      />
      <div class="video-container__play-icon" @click="showMediaDetail(url)">
        <div class="el-icon-video-play" />
      </div>
      <monitor ref="monitor" :monitor-info="monitorInfo" />
    </div>
  </div>
</template>

<script>
import Monitor from '@/components/Monitor'

export default {
  name: 'VideoItem',
  components: {
    Monitor
  },
  props: {
    url: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      monitorInfo: {}
    }
  },
  methods: {
    showMediaDetail(url) {
      this.monitorInfo = {
        type: 'video',
        meta: { url }
      }
      this.$refs.monitor.show()
    }
  }
}
</script>

<style scoped lang="scss">
.video-container {
  position: relative;
  margin: 0 auto 20px;
  width: 480px;
  text-align: center;
  cursor: pointer;
  &__play-icon {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.3);
    cursor: pointer;
    div {
      position: absolute;
      top: 50%;
      left: 50%;
      font-size: 35px;
      color: #fff;
      transform: translate(-50%, -50%);
      &:hover {
        color: #2d8cf0;
      }
    }
  }
}
</style>
