<template>
  <batch-detail-form
    :baby-num="babyNum"
    :task-detail="taskDetail"
  ></batch-detail-form>
</template>

<script>
import { checkDesc } from '@/api/healthManagement'
import BatchDetailForm from './components/BatchDetailForm'
export default {
  name: 'CanceledBatch',
  components: {
    BatchDetailForm
  },
  data() {
    return {
      taskDetail: {},
      babyNum: 0,
      taskId: ''
    }
  },
  mounted() {
    this.babyNum = this.$route.query.babyNum
    this.taskId = this.$route.query.taskId
    checkDesc(this.taskId, { type: 1 }).then(res => {
      if (res.code === 20000) {
        this.taskDetail = res.data
      }
    })
  }
}
</script>
